import React, { useState, useRef, useEffect } from 'react';
import test from '../../../assets/test.mp4';
import testImage from '../../../assets/img-place-holder.jpeg';
import testLogo1 from '../../../../public/images/clients/client03.png';
import testLogo3 from '../../../../public/images/clients/client04.png';
import testLogo from '../../../../public/images/clients/client05.png';
import testLogo2 from '../../../../public/images/clients/client06.png';

interface VideoSource {
  url: string;
  imageUrl: string;
  title: string;
  subtitle: string;
}

const VideoPlayer: React.FC = () => {
  const [currentVideo, setCurrentVideo] = useState(0);
  const videoRef = useRef<HTMLVideoElement>(null);
  const containerRef = useRef<HTMLDivElement>(null);
  const [videoLoading, setVideoLoading] = useState(true);
  const [showButtonContent, setShowButtonContent] = useState(false);
  const [isHovered, setIsHovered] = useState<number | null>(null);
  const [selectedButtonIndex, setSelectedButtonIndex] = useState<number | null>(null);

  const buttonLogoMap = [testLogo1, testLogo2, testLogo3, testLogo];

  const videoSources: VideoSource[] = [
    {
      url: test,
      imageUrl: testImage,
      title: 'MR. LEO WAN',
      subtitle: 'MARKETING DIRECTOR',
    },
    {
      url: test,
      imageUrl: testImage,
      title: 'MS. NGO MINH NGOC',
      subtitle: 'ARENA MARKETING MANAGER',
    },
    {
      url: test,
      imageUrl: testImage,
      title: 'MR. NGUYEN VAN A',
      subtitle: 'ARENA MARKETING MANAGER',
    },
    {
      url: test,
      imageUrl: testImage,
      title: 'MR. NGUYEN VAN B',
      subtitle: 'ARENA MARKETING MANAGER',
    },
  ];

  useEffect(() => {
    const observer = new IntersectionObserver(
      ([entry]) => {
        if (entry.isIntersecting && videoRef.current) {
          videoRef.current.load();
          videoRef.current.muted = true;
          videoRef.current.play().catch((error) => {
            console.error('Autoplay failed:', error);
          });
        } else if (videoRef.current) {
          videoRef.current.pause();
        }
      },
      { threshold: 0.5 },
    );

    if (containerRef.current) {
      observer.observe(containerRef.current);
    }

    return () => {
      if (containerRef.current) {
        observer.unobserve(containerRef.current);
      }
    };
  }, []);

  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.addEventListener('canplay', () => {
        setVideoLoading(false);
      });
      videoRef.current.load();
      videoRef.current.play().catch((error) => {
        console.error('Autoplay failed:', error);
      });
    }
  }, [currentVideo]);

  const handleVideoChange = (index: number) => {
    setVideoLoading(true);
    setCurrentVideo(index);
    setSelectedButtonIndex(index);
  };

  const handleVideoEnded = () => {
    const nextVideoIndex = (currentVideo + 1) % videoSources.length;
    setCurrentVideo(nextVideoIndex);
    setSelectedButtonIndex(nextVideoIndex);
  };

  useEffect(() => {
    if (videoRef.current) {
      videoRef.current.addEventListener('ended', handleVideoEnded);

      return () => {
        if (videoRef.current) {
          videoRef.current.removeEventListener('ended', handleVideoEnded);
        }
      };
    }
  }, [currentVideo]);

  return (
    <div className="mx-auto max-w-screen-2xl p-10 bg-[#FEF7ED]">
      <div
        ref={containerRef}
        className="relative m-10 overflow-hidden rounded-lg bg-white shadow-md"
      >
        <div className="relative w-full overflow-hidden rounded-lg pt-[56.25%]">
          <video
            className={`absolute left-0 top-0 h-full w-full rounded-lg object-cover ${videoLoading ? 'fade-out' : 'fade-in'}`}
            ref={videoRef}
            src={videoSources[currentVideo].url}
            onEnded={handleVideoEnded}
          />
        </div>
      </div>

      <div className="relative m-10"> 
        <div className="grid grid-cols-4 gap-4 pb-2">
          {videoSources.map((video, index) => (
            <button
              key={index}
              className={`group relative pt-4 shrink-0 flex-grow flex items-center justify-center overflow-hidden rounded-full transition-opacity duration-600 ${
                index === currentVideo
                  ? 'shadow-md shadow-orange-100 ring-2 ring-orange-500'
                  : 'hover:bg-orange-100 hover:shadow-md hover:shadow-orange-100'
              }`}
              style={{ minWidth: '52px' }} 
              onMouseEnter={() => setIsHovered(index)}
              onMouseLeave={() => setIsHovered(null)} 
              onClick={() => handleVideoChange(index)}
            >
              <div
                className={`absolute inset-0 pb-0 flex items-center justify-center transition-opacity duration-300 ${isHovered !== index && index !== selectedButtonIndex ? 'opacity-100' : 'opacity-0'}`}
                style={{ backgroundColor: '#67585517' }}
              >
                <img src={buttonLogoMap[index]} alt={`Logo for ${video.title}`} className="h-8 w-8" /> 
              </div>
              <div className={`flex pb-5 items-center space-x-2 rounded-[10px] transition-opacity duration-300 ${isHovered === index || index === selectedButtonIndex ? 'opacity-100' : 'opacity-0'}`}>
                <div className='relative'>
                  <img
                    src={video.imageUrl}
                    alt={`Profile of ${video.title}`}
                    className="h-8 w-8 rounded-full object-cover"
                  />
                  <div className="absolute inset-0 rounded-full border-2 border-[#C2BCB6]"></div>
                </div>
                <div className="flex-grow">
                  <div className="text-left text-sm font-bold">{video.title}</div>
                  <div className="text-left text-xs">{video.subtitle}</div>
                </div>
              </div>
            </button>
          ))}
        </div>
      </div>
    </div>
  );
};

export default VideoPlayer;
