// eslint-disable-next-line @typescript-eslint/no-unused-vars
import styles from './app.module.css';
import MainLayout from './components/layout/MainLayout';
import {
  Services,
  OurWork,
  AboutUs,
  Contact,
  Careers,
  News,
  FeaturedCampaigns
} from './components/features';
import { Routes, Route } from 'react-router-dom';
import gsap from 'gsap';
import { useGSAP } from '@gsap/react';
import { ScrollToPlugin, ScrollTrigger } from 'gsap/all';
import SliderWithScrollingX from './components/Slider/SliderWithScrollingX';
import SliderWithScrollingY from './components/Slider/SliderWithScrollingY';
import ListComponent from './components/InteractiveList/List';
import Clients from './components/Clients';
import Hero from './components/Hero';
import VideoPlayer from './components/video-player-showcase/videoPlayer';
import BackToTopButton from './components/BackToTheTopButton/backToTopButton';
import TeamIntroduction from './components/introduction/introductionBanner';

gsap.registerPlugin(ScrollToPlugin, ScrollTrigger, useGSAP);

export function App() {
  return (
    <div className={styles.mainLayoutContainer}>
      <MainLayout>
        <Routes>
          <Route
            path="/"
            element={
              <div>
                <Hero />
                <TeamIntroduction />
                <ListComponent />
                <Clients />
                <VideoPlayer />
                <SliderWithScrollingX />
                {/* <SliderWithScrollingY /> */}
                <div>
                </div>
              </div>
            }
          />
          <Route path="/services" element={<Services />} />
          <Route path="/about" element={<AboutUs />} />
          <Route path="/our-work" element={<OurWork />} />
          <Route path="/contact" element={<Contact />} />
          <Route path="/careers" element={<Careers />} />
          <Route path="/news" element={<News />} />
          <Route path="/featured-campaigns" element={<FeaturedCampaigns />} />
        </Routes>
        <BackToTopButton />
      </MainLayout>
    </div>
  );
}

export default App;
