import React, { useEffect, useState } from 'react';
import img from '../../../../public/images/projects/project1.png';
import img1 from '../../../../public/images/projects/project2.png';
import img2 from '../../../../public/images/projects/project3.png';

type Campaign = {
  src: string;
  title: string;
};

type Campaigns = {
  [key: string]: Campaign[];
};

function FeaturedCampaigns() {
  const [selectedOption, setSelectedOption] = useState<string | null>(null);
  
  useEffect(() => {
    setSelectedOption(options[0].value);
  }, []);

  const options = [
    { value: 'marketing-communications', label: 'Marketing Communications' },
    { value: 'event-management', label: 'Event Management & Brand Activation' },
    { value: 'merchandising', label: 'Merchandising & POSM' },
  ];

  const campaigns: Campaigns = {
    'marketing-communications': [
      { src: img, title: 'Campaign A' },
      { src: img, title: 'Campaign B' },
      { src: img, title: 'Campaign C' },
      { src: img, title: 'Campaign D' },
      { src: img, title: 'Campaign E' },
    ],
    'event-management': [
      { src: img1, title: 'Campaign C' },
      { src: img1, title: 'Campaign D' },
      { src: img1, title: 'Campaign E' },
      { src: img1, title: 'Campaign F' },
    ],
    'merchandising': [
      { src: img2, title: 'Campaign E' },
      { src: img2, title: 'Campaign F' },
      { src: img2, title: 'Campaign G' },
      { src: img2, title: 'Campaign H' },
    ],
  };

  return (
    <div className="mx-auto max-w-screen-2xl p-10">
      <h2 className="relative mb-8 text-5xl font-semibold">
        Featured Campaigns
      </h2>

      <div className="mb-8 flex space-x-4">
        {options.map((option) => (
          <button
            key={option.value}
            className={`rounded-md px-4 py-2 ${
              selectedOption === option.value
                ? 'bg-primary text-white'
                : 'bg-gray-200 text-gray-700'
            }`}
            onClick={() => setSelectedOption(option.value)}
          >
            {option.label}
          </button>
        ))}
      </div>

      {selectedOption !== null && ( // Check if selectedOption has a value
        <div className="flex flex-wrap overflow-x-auto gap-10">
          {campaigns[selectedOption].map((campaign, index) => (
            <div
              key={index} 
              className="group relative overflow-hidden rounded-md shrink-0 mr-4"
            >
              <img
                src={campaign.src}
                alt={campaign.title}
                className="w-full h-auto transform transition-transform duration-300 group-hover:scale-110"
              />
              <p className="absolute bottom-2 left-2 text-white font-semibold">
                {campaign.title}
              </p>
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

export default FeaturedCampaigns;
